#container {
  width: 720px;
  height: 720px;
  /* border-style: solid;
  border-width: 10px; */
  margin: 0 auto;
  position: relative;
}
canvas {
  background: url("img/pitch3.jpg") center;
  float: left;
  z-index: 1;
}

#selectTeam {
  border: white solid 1px;
  text-align: center;
  margin-top: 20px;
  padding-bottom: 10px;
  padding-top: 5px;
}

#startScreen {
  z-index: 2;
  width: 720px;
  height: 720px;
  background-image: url('img/splash.jpg');
  position:absolute;
  font-family: verdana;
  color: white;
}

ul {
  columns: 4;
  list-style-type: none;
  text-align: left;
  font-size: 15px;
}

#selections {
  margin-top: 15px;
}
#gameOverScreen {
  z-index: -1;
  width: 480px;
  height: 720px;
  background-image: url('img/splash.jpg');
  position: absolute;
  font-family: verdana;
  color: white;
}
#score {
  background-image: url('img/background.jpg');
  float: right;
  width: 240px;
  height: 690px;
  font-family: verdana;
  text-align: center;
  padding-top: 30px;
  z-index: 1;
}
#opponentName {
  color: #919191;
}
#ownName {
  color: #E03333;
}
#own,
#opponent {
  font-size: 48px;
  font-weight: bold;
}
#timer {
  padding-bottom: 100px;
  font-size: 28px;
  font-weight: bold;
}


#title {
  font-size: 50px;
  font-weight: bold;
  font-family: verdana;
  color: white;
  text-align: center;
  padding-top: 100px;
}
#play {
  margin-left: 300px;
  margin-top: 50px;
  width: auto;
  margin-bottom: 100px;
  padding: 20px 35px;
  background: white;
  border: 0;
  font-size: 20px;
  color: #424242;
  font-weight: bold;
  font-family: verdana;
  border-radius: 4px;
  margin-bottom: 10px;
  cursor: pointer;
}
#playagain, #backHome {
  margin-left: 140px;
  margin-top: 100px;
  width: auto;
  margin-bottom: 100px;
  padding: 20px 35px;
  background: white;
  border: 0;
  font-size: 20px;
  color: #424242;
  font-weight: bold;
  font-family: verdana;
  border-radius: 4px;
  margin-bottom: 10px;
}
#help {
  padding-left: 285px;
  opacity: 0.7;
}
#instructions {
  font-size: 12px;
  padding-left: 250px;
  text-align: center;
  opacity: 0.7;
}
#right {
  padding-left: 70px;
  font-size: 12px;
  text-align: center;
  opacity: 0.7;
}
#foot {
  margin-top: 160px;
  text-align: center;
  font-size: 10px;
  opacity: 0.5;
}
#status {
  margin-top: 100px;
  font-weight: bold;
  font-size: 20px;
}


#alertMessage, #errorMessage {
  /* display: none; */
  font-size: 10px;
  text-align: center;
}

footer {
  font-family: fantasy;
  color: lightcoral;
}
